<template>
    <div class="m-fb-rank-mini" v-if="ready">
        <h3 class="c-sidebar-right-title">
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><g fill="#ffa91e"><path d="M324.891 167.266l16.262 94.815L256 217.314l-85.153 44.767 16.262-94.815-68.891-67.16 95.205-13.833L256 0l42.577 86.273 95.205 13.833z"/><path d="M409.944 253.46v87.673L256.9 403.303l-.9-.36-153.944-61.76V253.46L256 315.219z"/><path d="M409.944 362.157v87.673L256.9 512l-.9-.36-153.944-61.76v-87.723L256 423.916z"/></g><g fill="#ff8900"><path d="M341.153 262.081L256 217.314V0l42.577 86.273 95.205 13.833-68.891 67.16zM409.944 253.46v87.673L256.9 403.303l-.9-.36v-87.724zM256 423.916l153.944-61.759v87.673L256.9 512l-.9-.36z"/></g></svg>全服排行
        </h3>
        <el-tabs @tab-click="loadRank" v-model="active">
            <el-tab-pane
                v-for="(cid, name) in nav"
                :label="name"
                :key="name"
                :name="name"
            ></el-tab-pane>
        </el-tabs>
        <div class="m-fb-rank-box" v-for="(group, i) in rank" :key="i">
            <div class="m-fb-rank-group" v-show="active == i">
                <ul class="u-list">
                    <li v-for="(item, j) in group" :key="j">
                        <span class="u-order">{{ j + 1 }}</span>
                        <span class="u-team">{{ item.leader || item.Role }}</span>
                        <span class="u-server">{{ item.Server }}</span>
                    </li>
                </ul>
            </div>
        </div>
        <a
            class="u-more el-button el-button--default is-plain el-button--mini"
            href="/fb/#/rank"
            >&raquo; 查看更多</a
        >
    </div>
</template>

<script>
import { getMiniRank } from "../service/getRank";
import moment from "moment";
// import mock from '../mock/rank.json'

export default {
    name: "minirank",
    props: [],
    data: function() {
        return {
            active: "安小逢",
            rank: {},
            ready: false,
            nav: {
                安小逢: "7529",
                周贽: "7525",
                厌夜: "7526",
                迟驻: "7527",
                白某: "7528",
            },
        };
    },
    methods: {
        loadRank: function() {
            // 状态设置
            this.loading = true;

            // 已请求
            if (this.rank[this.active]) {
                this.loading = false;
                return;
            }

            // 新请求
            let cid = this.nav[this.active];
            return getMiniRank(cid)
                .then((res) => {
                    this.$set(this.rank, this.active, res.data.data);
                })
                .catch((err) => {
                    console.log(err);
                })
                .finally(() => {
                    this.loading = false;
                });
        },
    },
    mounted: function() {
        this.loadRank().then(() => {
            this.ready = true;
        });
    },
    components: {},
};
</script>

<style lang="less">
@import "../assets/css/rank.less";
</style>